.language {
    position: relative
}

.language-list {
    width: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background: var(--color-base);
    padding: 10px;
    border-radius: 10px;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease-in-out;
    box-shadow: 0 4px 34px 0 rgba(0, 0, 0, .14);
    z-index: 100
}

.language-list li:not(:last-child) {
    margin-bottom: 4px
}

.language-list.open {
    top: 75px;
    opacity: 1;
    visibility: visible
}

.language-list.open ~ .language-toggle .language-arrow {
    transform: rotate(180deg)
}

.language-link {
    cursor: pointer;
    position: relative;
    color: var(--color-black);
    font-weight: 500;
    font-size: 16px;
    transition: all .3s ease-in-out;
    background: transparent;
    width: 100%;
    padding: 6px;
    border-radius: 6px
}

.language-link.active, .language-link:hover {
    background: rgba(0, 0, 0, .14);
    color: var(--color-black)
}

.language-text span {
    color: var(--color-base);
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin: 0 7px
}

.language .language-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0 20px 20px;
    background: var(--color-dark);
    box-shadow: 0 4px 34px 0 rgba(0, 0, 0, .14)
}

